import { mapActions } from "vuex";

export default {
    name: 'register',
    data() {
        return {
            username: '',
            password: ''
        }
    },

    methods: {
        ...mapActions(['register']),

        onRegister() {
            this.login({username: this.username, password: this.password})
              .then(()=>{
                this.$router.push({path: this.$route.query.redirect || '/'}) //
              })
        }
    }
}


